<template>
  <v-app light>
    <main>
      <v-container fluid fill-height>
        <v-layout>
          <v-flex xs6 offset-xs3>

            <!-- -->
            <form v-if="command === 'login'" @submit.prevent="login">
              <v-card>
                <v-card-title primary-title>
                  <v-layout row wrap>
                    <v-flex xs12>
                      
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="username"
                        label="E-mail"
                        class="mt-5"
                        type="email"
                        required
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="password"
                        label="Enter your password"
                        class="input-group--focused"
                        type="password"
                        required
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-card-title>
                <v-card-actions>
                  <v-btn type="submit" flat class="orange--text">Login</v-btn>
                  <v-btn v-on:click="move('signup')" flat class="orange--text">Sign Up</v-btn>
                  <v-btn v-on:click="move('forgot')" flat class="orange--text">Forgot Password?</v-btn>
                </v-card-actions>
              </v-card>
            </form>

            <!-- -->
            <form v-if="command === 'signup'" @submit.prevent="signup">
              <v-card>
                <v-card-title primary-title>
                  <v-layout row wrap>
                    <v-flex xs12>
                       
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="username"
                        label="E-mail"
                        class="mt-5"
                        type="email"
                        required
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="password"
                        label="Enter your password"
                        class="input-group--focused"
                        type="password"
                        required
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="name"
                        label=""
                        class="input-group--focused"
                        required
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-card-title>
                <v-card-actions>
                  <v-btn type="submit" flat class="orange--text"></v-btn>
                  <v-btn v-on:click="move('login')" flat class="orange--text"> </v-btn>
                  <v-btn v-on:click="move('forgot')" flat class="orange--text">Forgot Password?</v-btn>
                </v-card-actions>
              </v-card>
            </form>

            <!--  -->
            <form v-if="command === 'forgot'" @submit.prevent="forgot">
              <v-card>
                <v-card-title primary-title>
                  <v-layout row wrap>
                    <v-flex xs12>
                       
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="username"
                        label="E-mail"
                        class="mt-5"
                        type="email"
                        required
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-card-title>
                <v-card-actions>
                  <v-btn type="submit" flat class="orange--text"></v-btn>
                  <v-btn v-on:click="move('login')" flat class="orange--text"> </v-btn>
                  <v-btn v-on:click="move('signup')" flat class="orange--text">Sign Up</v-btn>
                </v-card-actions>
              </v-card>
            </form>

            <!--    -->
            <form v-if="command === 'edit-password'" @submit.prevent="editPassword">
              <v-card>
                <v-card-title primary-title>
                  <v-layout row wrap>
                    <v-flex xs12>
                        .
                    </v-flex>
                    <v-flex xs12>
                      <v-text-field
                        v-model="password"
                        label="Enter your password"
                        class="mt-5"
                        type="password"
                        required
                      ></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-card-title>
                <v-card-actions>
                  <v-btn type="submit" flat class="orange--text"></v-btn>
                  <v-btn v-on:click="move('login')" flat class="orange--text"> </v-btn>
                  <v-btn v-on:click="move('signup')" flat class="orange--text">Sign Up</v-btn>
                </v-card-actions>
              </v-card>
            </form>


          </v-flex>
        </v-layout>
      </v-container>
    </main>
  </v-app>
</template>

<script>
  export default {
    name: 'Login',
    props: {
      iam: Object
    },
    data: function () {
      return {
        command: null,
        username: null,
        password: null,
        name: null,
        file: null,
        scope: 'uEngineSubscriptions/subscriptionsapi',
        myUrl: location.protocol + '//' + location.hostname + (location.port ? ':' + location.port : '')
      };
    },
    mounted() {
      this.renderByCommand();
    },
    watch: {
      '$route'(to, from) {
        this.renderByCommand();
      }
    },

    methods: {
      renderByCommand: function () {
        this.username = null;
        this.password = null;
        this.command = this.$route.params.command;

        //verification  
        if (this.command == 'sign-up-verification' || this.command == 'forgot-verification') {
          this.verification();
        }
      },
      verification: function () {
        var me = this;
        // verification  
        if (this.command == 'sign-up-verification') {
          me.iam.signUpVerification(me.$route.query.token)
            .done(function (User) {
              //Validate for user...
              console.log(User);

              //Compete signup
              me.iam.signUpAccept(me.$route.query.token)
                .done(function (User) {
                  me.$root.$children[0].success('  .   .');
                  me.$router.push({
                    path: '/auth/login'
                  })
                })
                .fail(function () {
                  me.$root.$children[0].error('     .');
                })
            })
            .fail(function () {
              me.$root.$children[0].error('     .');
            })
        }

        //forgot  verification  
        if (this.command == 'forgot-verification') {
          me.iam.forgotPasswordVerification(me.$route.query.token)
            .done(function (User) {
              //Validate for user...
              console.log(User);

              //Move to password edit route
              me.$router.push({
                path: '/auth/edit-password?token=' + me.$route.query.token + '&userName=' + User.userName
              })
            })
            .fail(function () {
              me.$root.$children[0].error('     .');
            })
        }
      },
      onFileChange: function (file, fileName) {
        //console.log(file);
        this.file = file;
      },
      move: function (command) {
        this.$router.push({
          path: '/auth/' + command
        })
      },
      editPassword: function(e){
        e.preventDefault();
        var me = this;

        //Compete repassword
        me.iam.forgotPasswordAccept(me.$route.query.token, me.$route.query.userName)
          .done(function (User) {
            me.$root.$children[0].success('   .   .');
            me.$router.push({
              path: '/auth/login'
            })
          })
          .fail(function () {
            me.$root.$children[0].error('      .');
          })
      },
      forgot: function (e) {
        e.preventDefault();
        var me = this;
        me.iam.forgotPassword(me.myUrl + '/auth/forgot-verification', me.username)
          .done(function () {
            //     
            me.$root.$children[0].success('   .  .');
            me.$router.push({
              path: '/auth/login'
            })
          })
          .fail(function (response) {
            //  
            if (response.status == 404) {
              me.$root.$children[0].error('   .');
            }
            // 
            else {
              me.$root.$children[0].error('     .');
            }
          })
      },
      signup: function (e) {
        e.preventDefault();
        var me = this;
        me.iam.signUp(me.myUrl + '/auth/sign-up-verification', {
          userName: me.username,
          userPassword: me.password,
          email: me.username,
          locale: 'ko_KR',
          name: me.name
        })
          .done(function () {
            //     
            me.$root.$children[0].success('  .  .');
            me.$router.push({
              path: '/auth/login'
            })
          })
          .fail(function (response) {
            //  
            if (response.status == 409) {
              me.$root.$children[0].error('   .');
            }
            //   
            else {
              me.$root.$children[0].error('     .');
            }
          })
      },
      login: function (e) {
        e.preventDefault();
        var me = this;
        me.iam.passwordCredentialsLogin(me.username, me.password, me.scope, 'JWT')
          .done(function (response) {
            localStorage['access_token'] = response['access_token'];
            localStorage['username'] = me.username;
            var tenant = me.username.split("@")[1];
            tenant = tenant.split(".")[0];
            localStorage['tenant'] = tenant;

            //    
            if (me.$route.query.redirect) {
              me.$router.push({
                path: me.$route.query.redirect
              })
            }
            // 
            else {
              me.$router.push({
                path: '/'
              })
            }
          })
          .fail(function () {
            //   
            me.$root.$children[0].error('   .');
          })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



